.photos-background{
    background-color: var(--warm-black);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 101;
}

.photos-page{
    z-index: 102;
    background-color: var(--warm-black);
    position: absolute;
    top: 58px;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
    left: 0;
    width: 100vw;
    height: 200vh;
    box-shadow: 0px -10px 15px rgba(0, 0, 0, 0.219);
}

#title-for-photo-page{
    color: var(--light-white);
}

.photo-container{
    background-color: var(--light-white);
    width: 20vw;
    height: 33vw;
    padding: 0.8vw;
    border-radius: 7px;
    position: relative;
    margin: 5px;
}

.photo-picture{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 85%;
    border-radius: inherit;
}

.photo-info{
    text-align: right;
    margin-top: 2vh;
    text-transform: uppercase;
    font-size: 1vw;
    /* background-color: red; */
}

#all-photos{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-bottom: 10vw;
    /* background-color: blue; */
    align-items: flex-start;
}

#photo-project-line{
    /* background-color: red; */
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;

    opacity: 0;
    animation-name: fade-up;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}

.photo-project-line1{
    margin-top: 10vh;

    animation-delay: 0.3s;
}

.photo-project-line2{
    margin-top: 0;

    animation-delay: 0.2s;
}

.photo-project-line3{
    margin-top: 20vh;

    animation-delay: 0.4s;
}


#Photo1, #Photo2, #Photo3, #Photo4, #Photo5, #Photo6{
    transition: 0.3s;
}

#Photo1:hover, #Photo2:hover, #Photo3:hover, #Photo4:hover, #Photo5:hover, #Photo6:hover{
    transform: scale(1.1);
    z-index: 300;
    box-shadow: 0px 0px 20px 13px rgba(0, 0, 0, 0.301);
}

#photo-1{
    background-image: url(../assets/myphotos/mp-greenhouse.JPG);
}

#photo-2{
    background-image: url(../assets/myphotos/mp-sanfran.JPG);
}

#photo-3{
    background-image: url(../assets/myphotos/mp-karjat.jpg);
}

#photo-4{
    background-image: url(../assets/myphotos/mp-boat.JPG);
}

#photo-5{
    background-image: url(../assets/myphotos/mp-greatfalls.jpg);
}

#photo-6{
    background-image: url(../assets/myphotos/mp-la.JPG);
}

@keyframes fade-up {
    0% {
        opacity: 0;
        transform: translateY(13vh);
    }
    100% {
        opacity: 100%;
        transform: translateY(0);
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}